import React, { Component } from 'react'
import asyncComponent from '../asyncComponent'
import './Medium.css'

const TopBar = asyncComponent(() =>
  import(/* webpackChunkName:"topBar" */ '../TopBar/TopBar.js')
)

export default class extends Component {
  state = {
    items: [],
    error: false
  }

  request = async () => {
    try {
      const json = await fetch(
        'https://api.rss2json.com/v1/api.json?rss_url=https%3A%2F%2Fapi.medium.com%2Ffeed%2F%40alfari'
      )
      const { items } = await json.json()
      this.setState({ items })
    } catch (err) {
      console.log(err)
      this.setState({ error: true })
    }
  }

  async componentDidMount() {
    this.request()
  }

  tryAgain = () => {
    this.setState({ error: false })
    this.request()
  }

  render() {
    const feed = this.state.items.map(el => {
      const miniDesc =
        el.description
          .replace(/<figcaption>.*<\/figcaption>|<[^>]*>/gi, '')
          .slice(0, 230) + ' ...'
      return (
        <div className="single-feed mb-2 card" key={el.link}>
          <img
            className="card-img-top medium-img"
            src={el.thumbnail}
            alt="Medium articles"
          />
          <div className="card-body">
            <h5 className="card-title title">{el.title}</h5>
            <p className="card-text">{miniDesc}</p>
            <a href={el.link} target="_blank" className="btn btn-primary">
              More
            </a>
          </div>
        </div>
      )
    })

    const loading = (
      <div className="loading-wrapper">
        <div className="loading" />
        <div className="loading" />
        <div className="loading" />
      </div>
    )

    return (
      <div>
        <TopBar
          link="https://medium.com/@alfari"
          bgColor="bg-primary"
          color="white"
          icon="fa-medium"
        >
          Medium
        </TopBar>
        <div className="medium item-container">
          {this.state.error && (
            <div className="text-center">
              <p>Failed to fetch</p>
              <button className="btn btn-primary" onClick={this.tryAgain}>
                Try Again
              </button>
            </div>
          )}
          {!this.state.items.length && !this.state.error && loading}
          <div className="container">{feed}</div>
        </div>
      </div>
    )
  }
}



